  margin-right: auto;
  width: 50%;
  opacity: 1;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.ScansIt .middle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.ScansIt .prototype:hover .ScansItPrototype {
  opacity: 0.7;
}

.ScansIt .prototype:hover .middle {
